:root {
  --font-sans: "Roboto", Arial, sans-serif;
  --blue: #00acdc;
}

body {
  font-family: var(--font-sans);
  letter-spacing: 0;
  text-transform: uppercase;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fff;
  margin: 0;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 100%;
}

.logo img {
  object-fit: contain;
  width: 40px;
  height: 40px;
}

.header {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  display: flex;
}

.container {
  justify-content: space-between;
  align-items: center;
  width: 1200px;
  margin: 0 auto;
  padding: 30px;
  display: flex;
}

.nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav-list li + li {
  margin-left: 30px;
}

.nav-list a {
  text-transform: uppercase;
  color: #000;
  justify-content: center;
  align-items: center;
  height: 80px;
  text-decoration: none;
  display: inline-flex;
  position: relative;
}

.nav-list a:hover {
  color: var(--blue);
}

.nav-list a:after {
  content: "";
  background-color: var(--blue);
  width: 0;
  height: 2px;
  transition: width .3s;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav-list a:hover:after {
  width: 100%;
}

.nav-list a[data-qa="hover"]:hover, .nav-list a.is-active {
  color: var(--blue);
}

.nav-list a.is-active:after {
  width: 100%;
}
